import React from 'react'
import MainLeftSide from './MainLeftSide'
import Resume1 from './Resume1' 
import Resume2 from './Resume2'
import Resume3 from './Resume3'
import {
  Route,
} from "react-router-dom";
import { useAppSelector } from '../app/hooks'
import { selectAllInfo } from '../features/counter/counterSlice'

function Main() {
  const AllInformation = useAppSelector(selectAllInfo);
    return (
        <div className='main'>
              
            <div className='Left_side'>
                <MainLeftSide />
            </div>

            <div className='Right_side'>
         {AllInformation.length != 0 ?
         <>
          <Route path="/Resume3">
            <Resume3 />
          </Route>
          <Route path="/Resume2">
            <Resume2 />
          </Route>
          <Route exact path="/">
            <Resume1 />
          </Route>
          </>
        :
        <>
          <h1 className="NoDataFound">Add Information first</h1>
        </>  
        }
            </div>
        </div>
    )
}

export default Main
