import React from "react";
import { useAppSelector } from "../app/hooks";
import { selectAllInfo } from "../features/counter/counterSlice";
import "./Resume1.css";

function Resume1() {
  const AllInformation = useAppSelector(selectAllInfo);
  return (
    <div className="resume1">
      <div className="resume1_header">
       <h1>{AllInformation.Name ? AllInformation.Name : '' }</h1>
        <p> {AllInformation.Address ? AllInformation.Address : '' } || ‚òéÔ∏è {AllInformation.Phone ? AllInformation.Phone : '' }</p>
      </div>
      <div className="resume1_Objective">
        <h2 className="heading">Objective</h2>
        <p>
        {AllInformation.Objective ? AllInformation.Objective : '' }
        </p>
      </div>
      <div className="resume1_Education">
        <h2 className="heading">Education</h2>
        <ul>
          {AllInformation.Education ? AllInformation.Education.map(e => <li>{e}</li>) : '' }
        </ul>
      </div>
      <div className="resume1_Skill">
        <h2 className="heading">Skills</h2>
        <ul>
        {AllInformation.Skills ? AllInformation.Skills.map(e => <li>{e}</li>) : '' }
        </ul>
      </div>
      <div className="resume1_Experience">
        <h2 className="heading">Experience</h2>
        <ul>
        {AllInformation.Experience ? AllInformation.Experience.map(e => <li>{e}</li>) : '' }
        </ul>
      </div>
      <div className="resume1_Habbits">
        <h2 className="heading">Hobbies</h2>
        <ul>
        {AllInformation.Hobbies ? AllInformation.Hobbies.map(e => <li>{e}</li>) : '' }
        </ul>
      </div>
      <div className="resume1_Projects">
        <h2 className="heading">Projects</h2>
        <ul>
        {AllInformation.Project ? AllInformation.Project.map(e => <li>{e}</li>) : '' }
        </ul>
      </div>
    </div>
  );
}

export default Resume1;
