import React, { useState } from 'react'
import { useHistory } from 'react-router'
import { useAppDispatch } from '../app/hooks'
import { AddData } from '../features/counter/counterSlice'

function InformationDetails() {
    const dispatch = useAppDispatch()
    const history =  useHistory();

    const [data, setdata] = useState({
        Name:"",
        Address:"",
        Phone:"",
        Objective:"",
        Education:[],
        Skills:[],
        Experience:[],
        Hobbies:[],
        Project:[],
    })

    const FormData = (e) =>{
        if(e.target.name === 'Skills' || 'Experience' || 'Education' || 'Hobbies' || 'Project')
        {
            setdata({...data,[e.target.name]:(e.target.value).split(",")})
        }else{
            setdata({...data,[e.target.name]:e.target.value})
        }
    }

    const HandleSubmit = async (e) =>{
        e.preventDefault();
        await dispatch(AddData(data));
        history.push('/');
    }

    return (
        <div className='Information'>
            <div className='Information_form'>
            <form onSubmit={HandleSubmit}>
            <input type="text" placeholder='Name' id="Name" name="Name" onChange={e => FormData(e)} value={data.name} /><br />
            <input type="text" placeholder='Address' id="Address"onChange={e => FormData(e)} name="Address" value={data.Address} /><br />
            <input type="text" placeholder='Phone No.' id="Phone" name="Phone" value={data.Phone} onChange={e => FormData(e)} /><br />
            <textarea placeholder='Objective' name="Objective" onChange={e => FormData(e)} value={data.Objective} /><br />
            <input type="text" placeholder='Add Multiple Education with Commas ( , )' id="Education" name="Education" onChange={e => FormData(e)} value={data.Education} /><br />
            <input type="text" placeholder='Add Multiple Skills with Commas ( , )' id="Skills" name="Skills" value={data.Skills} onChange={e => FormData(e)} /><br />
            <input type="text" placeholder='Add Multiple Experience with Commas ( , ) ' id="Experience" name="Experience" value={data.Experience} onChange={e => FormData(e)} /><br />
            <input type="text" placeholder=' Add Multiple Hobbies with Commas ( , )' id="Hobbies" name="Hobbies" value={data.Hobbies} onChange={e => FormData(e)} /><br />
            <input type="text" placeholder='Add Multiple Project with Commas ( , ) ' id="Project" name="Project" value={data.Project} onChange={e => FormData(e)} /><br />
            <button>Submit</button>
            </form> 

            </div>
        </div>
    )
}

export default InformationDetails
