import React, { useState } from 'react'
import image1 from '../../src/app/Image/screenshot.png'
import image2 from '../../src/app/Image/screenshot (2).png'
import image3 from '../../src/app/Image/screenshot (1).png'
import './Resume1.css'
import {
    Link
  } from "react-router-dom";

function MainLeftSide() {
    const [show, setshow] = useState('img3');
    return (
        <div>
            <div className='ImageDiv'>
           <Link to="/" > <img className={"scollImages" } src={image3} onClick={() => setshow('img3')}   className='scollImages' alt="img3" />   </Link>    
           <div >{(show === 'img3') ? "üëç" : ''}</div>
            </div>

            <div className='ImageDiv'>
           <Link to="/Resume2" > <img className={ "scollImages" } src={image2} onClick={() => setshow('img2')}  className='scollImages' alt="img2" />  </Link>    
           <div >{(show === 'img2') ? "üëç" : ''}</div> 
            </div>

            <div className='ImageDiv'>
           <Link to="/Resume3" > <img className={"scollImages" } src={image1} onClick={() => setshow('img1')}  className='scollImages' alt="img1" /> </Link>     
           <div>{(show === 'img1') ? "üëç" : ''}</div>
            </div>
        </div>
    )
}

export default MainLeftSide
